@mixin rippleEffect($color: unset) {
    .ripple {
        position: absolute;

        transform: scale(0);

        animation: ripple 600ms linear;

        border-radius: var(--ym-radius-size-round);

        background-color: $color;

        @media (prefers-reduced-motion: reduce) {
            animation: none;
        }
    }

    @keyframes ripple {
        to {
            transform: scale(4);

            opacity: 0;
        }
    }
}
