@import '@yandex-music-int/sol/mixins/rippleEffect';

.root {
    width: 100%;
    height: 56px;

    .paper {
        display: flex;

        transition: background-color var(--ym-duration-transition);

        cursor: pointer;

        padding-inline-start: var(--ym-spacer-size-xs);
        padding-inline-end: var(--ym-spacer-size-xs);

        height: 100%;
        max-height: 100%;

        @media (prefers-reduced-motion: reduce) {
            transition: none;
        }
    }

    &:hover,
    &:focus,
    &:focus-within {
        .paper {
            background-color: var(--ym-surface-color-primary-enabled-list);
        }
    }

    @include rippleEffect(var(--ym-controls-color-secondary-default-pressed));
}
