@import '~@yandex-music-int/sol/mixins/focusOutline.scss';

.root {
    --background-z-index: 1;
    --control-z-index: 2;
    --cover-size: 40px;

    display: flex;
    flex-direction: column;
    justify-content: center;

    width: var(--cover-size);
    height: 100%;

    .cover {
        width: var(--cover-size);
        max-width: var(--cover-size);
        height: var(--cover-size);
        max-height: var(--cover-size);
    }

    .coverImage {
        transition: filter var(--ym-duration-transition);

        filter: grayscale(0);

        @media (prefers-reduced-motion: reduce) {
            transition: none;
        }
    }

    .cover::before {
        position: absolute;
        z-index: var(--background-z-index);

        will-change: opacity;
        transition: opacity var(--ym-duration-transition);

        opacity: 0;

        background-color: var(--ym-surface-color-primary-enabled-entity);

        width: 100%;
        height: 100%;

        content: '';

        inset-block-start: 0;
        inset-inline-start: 0;

        @media (prefers-reduced-motion: reduce) {
            transition: none;
        }
    }

    .control {
        display: flex;
        position: absolute;
        inset-block-start: 0;
        inset-inline-start: 0;

        z-index: var(--control-z-index);

        align-items: center;
        justify-content: center;

        will-change: opacity;
        transition: opacity var(--ym-duration-transition);

        opacity: 0;

        background-color: var(--ym-surface-color-primary-enabled-list);

        width: 100%;
        height: 100%;

        @media (prefers-reduced-motion: reduce) {
            transition: none;
        }
    }

    .playButton {
        will-change: transform;
        transition: transform var(--ym-duration-transition);

        &:hover,
        &:focus {
            transform: scale(1.05);
        }

        @include focusOutline(var(--ym-controls-color-secondary-default-focused_stroke));

        @media (prefers-reduced-motion: reduce) {
            transition: none;
        }
    }

    &_current,
    &_playing {
        .control {
            opacity: 1;
        }

        .cover::before {
            opacity: 0.8;
        }
    }

    &:not(&_disabled) {
        :global(.trackChildrenVisibility:hover) &,
        :global(.trackChildrenVisibility:focus-within) &,
        :global(.trackChildrenVisibility:focus) & {
            .control {
                opacity: 1;
            }

            .playingAnimation {
                display: none;
            }

            .playButton {
                display: block;
            }

            .cover::before {
                opacity: 0.8;
            }
        }
    }

    &_disabled,
    :global(.trackChildrenVisibility:not(:hover, :focus, :focus-within)) &_disliked {
        .coverImage {
            filter: grayscale(1);
        }
    }

    &_current#{&}_playing {
        .playingAnimation {
            display: block;
        }

        .playButton {
            display: none;
        }

        .cover::before {
            opacity: 0.8;
        }
    }
}

.playButtonIcon {
    fill: var(--ym-controls-color-secondary-text-enabled_variant);
}
