// Импорт root класса из стилей трека
@value root as track_root from '../../../Track/Track.module.scss';

.root {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: var(--ym-spacer-size-xxxs);

    width: 28px;
    height: 100%;
}

.position {
    display: flex;
    align-items: center;
    justify-content: center;

    will-change: color;
    transition: color var(--ym-duration-transition);

    color: var(--ym-controls-color-primary-text-enabled);

    &_disliked {
        .track_root:not(:hover, :focus, :has(:focus-visible)) & {
            color: var(--ym-controls-color-primary-text-disabled);
        }
    }

    &_disabled {
        color: var(--ym-controls-color-primary-text-disabled);
    }

    @media (prefers-reduced-motion: reduce) {
        transition: none;
    }
}

.progress {
    will-change: fill;
    transition: fill var(--ym-duration-transition);

    &_up {
        fill: var(--ym-message-color-successful-text-enabled);
    }

    &_same {
        fill: var(--ym-controls-color-primary-text-enabled);
    }

    &_down {
        fill: var(--ym-message-color-error-text-enabled);
    }

    &_new {
        fill: var(--ym-controls-color-primary-default-enabled);
    }

    &_disliked {
        .track_root:not(:hover, :focus, :has(:focus-visible)) & {
            fill: var(--ym-controls-color-primary-text-disabled);
        }
    }

    &_disabled {
        fill: var(--ym-controls-color-primary-text-disabled);
    }

    @media (prefers-reduced-motion: reduce) {
        transition: none;
    }
}
