@import '~@yandex-music-int/sol/mixins/breakpoints.scss';
@import '~@yandex-music-int/sol/mixins/focusOutline.scss';
// Импорт root класса из стилей трека
@value root as track_root from '../../../Track/Track.module.scss';

.root {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 16px;

    height: 100%;
}

.item {
    display: flex;
    position: relative;

    align-items: center;
    justify-content: center;

    will-change: opacity;
    transition: opacity var(--ym-duration-transition);

    width: 40px;
    height: 40px;

    color: var(--ym-controls-color-primary-text-enabled);
    fill: var(--ym-controls-color-primary-text-enabled);

    @media (prefers-reduced-motion: reduce) {
        transition: none;
    }
}

.contextMenu {
    flex-shrink: 0;

    opacity: 0;

    color: var(--ym-controls-color-primary-text-enabled);
    fill: var(--ym-controls-color-primary-text-enabled);

    &:not(:disabled) {
        &:hover,
        &:focus-visible {
            color: var(--ym-controls-color-primary-text-enabled_variant);
            fill: var(--ym-controls-color-primary-text-enabled_variant);
        }

        &:focus-visible {
            @include focusOutline(var(--ym-controls-color-secondary-default-focused_stroke));
        }
    }
}

.duration {
    position: absolute;

    transform: translate3d(-50%, -50%, 0);

    will-change: opacity;
    transition: opacity var(--ym-duration-transition);

    opacity: 1;

    inset-block-start: 50%;
    inset-inline-start: 50%;

    [dir='rtl'] & {
        transform: translate3d(50%, -50%, 0);
    }

    @media (prefers-reduced-motion: reduce) {
        transition: none;
    }
}

.controls {
    .dislikeIcon {
        opacity: 0;
    }

    &_disabled {
        .item {
            color: var(--ym-controls-color-primary-text-disabled);
            fill: var(--ym-controls-color-primary-text-disabled);
        }
    }

    .item {
        &:not(:disabled) {
            &:hover,
            &:focus-visible {
                color: var(--ym-controls-color-primary-text-enabled_variant);
                fill: var(--ym-controls-color-primary-text-enabled_variant);
            }

            &:focus-visible {
                @include focusOutline(var(--ym-controls-color-secondary-default-focused_stroke));
            }
        }
    }

    &_disliked {
        .track_root:not(:hover, :focus, :has(:focus-visible)) & {
            .item {
                color: var(--ym-controls-color-primary-text-disabled);
                fill: var(--ym-controls-color-primary-text-disabled);
            }
        }

        .dislikeIcon {
            opacity: 1;
        }

        .likeIcon {
            opacity: 0;
        }
    }

    .track_root:hover &,
    .track_root:focus &,
    .track_root:has(:focus-visible) & {
        .likeIcon {
            opacity: 1;
        }

        .dislikeIcon {
            opacity: 1;
        }

        .contextMenu {
            opacity: 1;
        }

        .duration {
            opacity: 0;
        }
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        .likeIcon {
            display: none;
        }

        .dislikeIcon {
            display: none;
        }

        .duration {
            opacity: 0;
        }

        .contextMenu {
            opacity: 1;
        }
    }
}

.popover {
    width: 100%;
    max-width: 300px;

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        width: 100%;
        max-width: 100%;
        max-height: 50vh;
    }
}
