.root {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 28px;

    .playButtonIcon,
    .playButton {
        padding: 0;

        width: 28px;
        height: 28px;
    }

    .playButton {
        // связка position: absolute и opacity: 0 заменяет display: none, но позволяет элементу быть доступным a11y
        position: absolute;

        opacity: 0;
    }

    .position {
        display: flex;
        align-items: center;
        justify-content: center;

        width: 28px;
        height: 28px;

        color: var(--ym-controls-color-primary-text-enabled);
    }

    .playButtonIcon {
        transition: fill var(--ym-duration-transition);

        fill: var(--ym-controls-color-primary-text-enabled);

        @media (prefers-reduced-motion: reduce) {
            transition: none;
        }
    }

    .playButton:hover .playButtonIcon,
    .playButton:focus .playButtonIcon {
        fill: var(--ym-controls-color-primary-text-hovered_variant);
    }

    &_current,
    &_playing {
        .position {
            display: none;
        }
    }

    &_current {
        .playButton {
            position: static;

            opacity: 1;
        }

        .playingAnimation {
            display: none;
        }
    }

    &_playing {
        .playButton {
            position: absolute;

            opacity: 0;
        }

        .playingAnimation {
            display: block;
        }
    }

    &_disabled,
    :global(.trackChildrenVisibility:not(:hover, :focus, :focus-within)) &_disliked {
        .position {
            color: var(--ym-controls-color-primary-text-disabled);
        }
    }

    &:not(&_disabled) {
        :global(.trackChildrenVisibility:hover) &,
        :global(.trackChildrenVisibility:focus-within) &,
        :global(.trackChildrenVisibility:focus) & {
            .playingAnimation {
                display: none;
            }

            .playButton {
                position: static;

                opacity: 1;
            }

            .position {
                display: none;
            }
        }
    }
}
